import React, { useState, useContext, useEffect } from "react";
import { AuthContext } from "../../auth/AuthContext";
import * as S from "./styles";
import { useHistory } from "react-router-dom";
import "./style.css";
import ListaHok from "../../dados/hooks/listaHok";
import Layout from "../../layout";
import firebase from "firebase"

function Cart() {
  const [nameComprador, setNameComprador] = useState('')
  const [compradosArrayState, setCompradosArrayState] = useState(null)

  const [cumpom, setcumpom] = useState(false)
  const [cumpomInput, setcumpomInput] = useState(null)
  const [cumpomSelect, setCumpomSelect] = useState('123');




  const listaHok = ListaHok();
  const espetaculoUsuario = listaHok.listaEspetaculosDoUsuario;
  const { currentUser } = useContext(AuthContext);
  const history = useHistory();
  // console.log(espetaculoUsuario)
  if (!currentUser) {
    history.push("login");
  }
  const [temItem, setTem] = useState(false);

  useEffect(() => {
    if (espetaculoUsuario.key) {
      setTem(true);
    }
  }, [espetaculoUsuario.key]);

  const userId = firebase.auth().currentUser.uid;
  // console.log(userId)
  useEffect(() => {
    let compradosArray = firebase.database().ref("lancamentoVenda/" + userId);
    compradosArray.on("value", (snapshot) => {
      if (snapshot.val() !== null) {
        setCompradosArrayState(snapshot.val());
      }
    });
  }, [userId])

  async function payment() {
    let name = firebase?.database().ref('users/' + firebase.auth()?.currenUser?.uid + "/fullName")

    name.on('value', (snapshot) => {
      if (snapshot.val() !== null) {

        setNameComprador(snapshot.val())
      }
    })



    const data = new Date().toLocaleString("en-US", { timeZone: 'America/Sao_Paulo' })

    if (!compradosArrayState) {
      firebase.database().ref('lancamentoVenda/' + userId).set([{
        userId,
        data,
        peça: listaHok?.listaEspetaculosDoUsuario,
        valor: espetaculoUsuario.valorPreco,
        name: nameComprador,
        namePeca: listaHok?.listaEspetaculosDoUsuario.tituloEsp
      }])


      window.location.href = espetaculoUsuario.paymentLink;

    } else {


      var arrayList = compradosArrayState

      arrayList.push({
        userId,
        data,
        peça: listaHok?.listaEspetaculosDoUsuario,
        valor: espetaculoUsuario.valorPreco,
        name: nameComprador,
        namePeca: listaHok?.listaEspetaculosDoUsuario.tituloEsp
      })

      arrayList.reverse()

      firebase.database().ref('lancamentoVenda/' + userId).set(arrayList)

      window.location.href = espetaculoUsuario.paymentLink;

    }




  }

  return (
    <Layout>
      <S.Wrapper>
        <S.Container noItem={!temItem}>
          {
            !temItem ? (
              <S.CardFilmeTitle>
                <S.Cart>
                  <i className="bi bi-cart-x-fill"></i>
                </S.Cart>
                <span>
                  Encontre um de Nossos Espetáculos e adicione ao carrinho :D
                </span>
              </S.CardFilmeTitle>
            ) : (
              <>
                <S.Header>
                  <span>Carrinho de compras</span>
                </S.Header>
                <S.CartContainer>
                  <S.CartLinha>
                    <S.CartFilme>
                      <S.X>
                        <i className="bi bi-x-lg"></i>
                      </S.X>
                      <S.Img src={espetaculoUsuario.thumbnail}></S.Img>
                      <S.CartFilmeInfo>
                        <S.CardFilmeTitle>
                          {espetaculoUsuario.tituloEsp}
                        </S.CardFilmeTitle>
                        <S.CardFilmeSubTitle>
                          {espetaculoUsuario.tituloEsp}
                        </S.CardFilmeSubTitle>
                        <S.CardFilmeSubTitle>


                        </S.CardFilmeSubTitle>
                        <S.CardFilmeValueHor>
                          Você tem 24 horas para assistir quantas vezes quiser a
                          esse espetaculos após o pagamento.
                        </S.CardFilmeValueHor>
                      </S.CartFilmeInfo>
                    </S.CartFilme>
                    <S.CardFilmeValue>
                      R$ {espetaculoUsuario.valorPreco}
                    </S.CardFilmeValue>
                  </S.CartLinha>
                </S.CartContainer>
                <S.CartTotalContainer>
                  <span>Total R$: {espetaculoUsuario.valorPreco}</span>
                  <S.InputCumpom type={'text'} placeholder="PREENCHA SEU CUMPOM" value={cumpomInput} onChange={(e) => {
                    setcumpomInput(e.target.value)

                  }} className="inputCumpom" />


                </S.CartTotalContainer>
                {cumpomInput && cumpomInput != cumpomSelect &&
                  <div className="invalidotxt">
                    <S.Span cor="red" size="18">Cupom invalido</S.Span>

                  </div>

                }

                {cumpomInput == cumpomSelect &&
                  <div className="invalidotxt">
                    <S.Span cor="green" size="18">Cupom valido</S.Span>
                  </div>
                }
                <S.CartActions>
                  <S.ButtonVoltar
                    onClick={() =>
                      history.push(`/Cardezao/${espetaculoUsuario.key}`)
                    }
                  >
                    <i className="bi bi-chevron-left"></i>Voltar{" "}
                  </S.ButtonVoltar>

                  { }
                  {!cumpomInput &&
                    <S.ButtonPagar
                      onClick={() => payment()}
                    >
                      Pagar
                    </S.ButtonPagar>
                  }
                  {cumpomInput && cumpomInput != cumpomSelect &&
                    <div className="invalido">
                      <S.ButtonPagar style={{ backgroundColor: "rgba(130, 131, 133, 0.226)", cursor: "default" }}>
                        Pagar
                      </S.ButtonPagar>
                    </div>

                  }

                  {cumpomInput == cumpomSelect &&
                    <div className="invalido">
                      <S.ButtonPagar >
                        Pagar
                      </S.ButtonPagar>
                    </div>
                  }
                </S.CartActions>
              </>
            )
            /* {/*         <S.CartInfo>
          <span>
            Torne-se um assinante e garanta 50% de desconto neste conteúdo
          </span>
          <S.CartInfoText>
            E ainda tenha acesso a um amplo conteúdo em peças teatrais, shows musicais e palestras para assistir a qualquer hora(exceto lançamentos).
          </S.CartInfoText>
        </S.CartInfo>
        <S.CartActionsPlains>
              <S.ButtonPlanos
                onClick={() => history.push(`/pagamento/idespetaculo`)}
              >
                Planos
              </S.ButtonPlanos>
        </S.CartActionsPlains> */
          }{" "}
          */
        </S.Container>
      </S.Wrapper>
    </Layout>
  );
}

export default Cart;
