import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import { SwiperSlide } from "swiper/react";
import { useHistory } from "react-router";
import * as S from "./styles";
import Layout from "../../layout";

import Share from "../../components/CardCompartilhar/index";
import instagram from "../../assets/instagram-yellow.png";
import facebook from "../../assets/facebook-yellow.png";
import ImgIcon from '../../assets/icon.png'

import Section from "../../components/Section";
import Card from "../../components/Card";
import HorizontalList from "../../components/HorizontalList";
import CanalPwThumb from '../../assets/canalpwthumb.png'
import BannerThumb from '../../assets/bannerThumb.png'

import ListaTeste from "../Home/lista";
import listarCanais from "../Home/listaCanais";

function CanalPw() {

  const history = useHistory();
  const [compartilharFica, setcompartilharFicha] = useState(false);
  const [compartilhar, setcompartilhar] = useState(false);
  const [popap, setPopap] = useState(false);
  const [popap2, setPopap2] = useState(false);
  const listfak = ['1']

  const [canais, setcanais] = useState({});
  const params = useParams("/FichaCanal/:id");
  const { id } = params;


  return (
    <>
      {/** Popap 1 */}
      {!popap &&
        <Layout>
          <S.Container>
            <S.PopapDiv>
              <S.PopapDiv1>
                <S.Span cor="black" size="1.8" className="mb-5 mt-5" >O que é a TV PalcoWeb ?</S.Span>
                <S.Span cor="black" size="1.8" className="mt-2">Programa de entrevistas e palestras com diretores, produtores e artistas da área de teatro e cultural.</S.Span>
                <S.PopapDivButton>
                  <S.PopapButton onClick={() => (setPopap(!popap))}>
                    <S.Span size="1.5">Acessar</S.Span>
                  </S.PopapButton>
                </S.PopapDivButton>
              </S.PopapDiv1>
            </S.PopapDiv>
          </S.Container>
        </Layout>

      }

      {popap &&
        <Layout>
          <S.Container>
            <S.ContainerImg>
              <S.Thumbnail src={CanalPwThumb}></S.Thumbnail>
            </S.ContainerImg>

            {/** Popap 2 */}
            {!popap2 &&

              <S.PopapDivG2>
                <S.PopapDivP2 className="">
                  <S.PopapDivButtonp2>
                    <S.PopapButton onClick={() => (setPopap2(!popap2))} style={{ backgroundColor: "transparent" }}>
                      <S.Span size="1.5" cor="black"><i class="bi bi-x-lg"></i></S.Span>
                    </S.PopapButton>
                  </S.PopapDivButtonp2>
                  <S.Span cor="black" size="2">Estreia 07 de março  </S.Span>
                  <S.Span cor="black" size="2">19 horas ao vivo </S.Span>
                  <S.Span cor="black" size="1"> horario de Brasília </S.Span>

                </S.PopapDivP2>
              </S.PopapDivG2>


            }
            <S.Conteudo>
              <S.ButtonVoltar
                onClick={() => history.push("/canais") || window.location.reload()}
              >
                {" "}
                <i className="bi bi-x-lg"></i>{" "}
              </S.ButtonVoltar>

              <S.HeaderConteudo>
                <S.Item>
                  <S.Coracao>
                    <i className="bi bi-suit-heart"></i>
                  </S.Coracao>

                  <S.Compartilhar>
                    {" "}
                    <i
                      className="bi bi-share-fill"
                      onClick={() => setcompartilharFicha(true)}
                      style={{ cursor: "pointer" }}
                    ></i>
                    {compartilharFica && (
                      <Share
                        link={`http://www.palcoweb.com.br/canalpw`}
                        sairficha={setcompartilharFicha}
                      ></Share>
                    )}
                  </S.Compartilhar>
                </S.Item>
                <S.Img
                  src={ImgIcon}
                  style={{
                    margin: 0,
                    width: 100,
                    height: 100,
                    borderRadius: "50%",
                  }}
                  value={13}
                  alt="imagem referente ao canal"
                />
                <S.Item>
                  <S.HeaderTitle>
                    {" "}
                    <S.Span size="1">TV PalcoWeb</S.Span>
                  </S.HeaderTitle>
                  <div>
                    {/*redes sociais */}
                    {canais?.facebook && (
                      <a
                        href={canais.facebook}
                        target="_blank"
                        rel="noopener noreferrer"
                        style={{ color: "#fdcc12", textDecorationLine: "none" }}
                      >
                        {" "}
                        <img src={facebook} alt="logo do facebook" />
                      </a>
                    )}
                    {canais?.instagram && (
                      <a
                        href={"https://www.instagram.com/" + canais?.instagram}
                        target="_blank"
                        rel="noopener noreferrer"
                        style={{ color: "#fdcc12", textDecorationLine: "none" }}
                      >
                        {" "}
                        <img src={instagram} alt="logo do instagram" />
                      </a>
                    )}
                  </div>
                  {canais.site && (
                    <S.Span cor="#fdcc12" size="1" style={{ cursor: "pointer" }}>
                      {" "}
                      <a
                        href={canais.site}
                        target="_blank"
                        rel="noopener noreferrer"
                        style={{ color: "#fdcc12", textDecorationLine: "none" }}
                      >
                        Site Oficial
                      </a>{" "}
                    </S.Span>
                  )}
                </S.Item>
              </S.HeaderConteudo>

              <S.Informacoes>
                <S.Item>
                  <S.Text size="0.8">Tv palco web é um programa de variedades exibido na TV Palco web. O programa apresentado pelo CEO JORGE ALBERTO ZIETLOW DURO Graduado em Administração Pública e de Empresas pela UFRGS , Mestre em Administração pela PUC-Rio e Doutor pela PUC-Rio. Diretor da DataCorp Mercadata do Brasil Ltda. Consultor há mais de vinte anos e entusiasta nato pelas artes e culturas de todo mundo. Traçando um Roteiro intrigrante desvendando os mitos e misterios em suas entrevista e palestras com diretores, produtores e artistas da área de teatro e cultural.</S.Text>
                </S.Item>
                <S.Item>
                  <S.Span size="0.8">

                  </S.Span>
                </S.Item>
              </S.Informacoes>

            </S.Conteudo>
          </S.Container>

          <Section title="Entrevistas" filter>
            <HorizontalList id="emAlta">
              {listfak?.map((card, index) => {
                console.log(card)

                return (
                  <SwiperSlide key={String(index)}>
                    {card === "" ? (
                      <Card noContent />
                    ) : (
                      <S.CardTest>
                        <S.Img
                          src={BannerThumb}
                          style={{
                            position: "absolute",
                            width: 250,
                            height: 400,
                          }}
                          value={card.key}
                        />

                        {/**<S.InfoCard>
                            <S.InfoCardConteudo>
                              <S.ClassificacaoCard>
                                {card.classificacao}
                              </S.ClassificacaoCard>
                              <S.Coracao>
                                <i className="bi bi-suit-heart-fill"></i>
                              </S.Coracao>

                              <S.Compartilhar>
                                <i
                                  className="bi bi-share-fill"
                                  onMouseEnter={() => setcompartilhar(true)}
                                ></i>
                                {compartilhar && (
                                  <Share sair={setcompartilhar}></Share>
                                )}
                              </S.Compartilhar>
                            </S.InfoCardConteudo>

                            <S.Titulo>{card.title}</S.Titulo>
                            <S.ConteudoCard style={{ textTransform: "uppercase" }}>
                              {card.categoria}
                            </S.ConteudoCard>
                            <S.Sinopsia>{card.synopsis}</S.Sinopsia>

                            <S.ButtonAcessar
                              className="btn btn-warning"
                              onClick={() => {
                                history.push(`/cardezao/${card.key}`);
                                window.location.reload();
                              }}
                            >
                              Saiba Mais{" "}
                            </S.ButtonAcessar>
                            </S.InfoCard>**/}
                      </S.CardTest>
                    )}
                  </SwiperSlide>
                );

              })}
            </HorizontalList>
          </Section>
        </Layout>
      }

    </>
  );
}
export default CanalPw;